import React, {useState, useEffect} from 'react';
import ContainerHeader from 'components/ContainerHeader/index';
import IntlMessages from 'util/IntlMessages';
import CardBox from 'components/CardBox/index';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import AttachFile from '@material-ui/icons/AttachFile';
import {useSelector} from 'react-redux';
import {base_url,public_url, $http} from "config";
import CircularProgress from '@material-ui/core/CircularProgress';
import Avatar from '@material-ui/core/Avatar';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import Fab from '@material-ui/core/Fab';

const Profile = (props) => {

    const [loader, setLoader] = useState(false);
    const [first_name, setFirstName] = useState("");
    const [last_name, setLastName] = useState("");
    const [job_title, setJobTitle] = useState("");
    const [profile_img, setProfileImg] = useState(null);
    const [user_role, setUserRole] = useState("");
    const [contact_number, setContactNumber] = useState("");
    const [status, setStatus] = useState("");
    const [password, setPassword] = useState("");
    const [email_address, setEmailAddress] = useState("");
    const {width} = useSelector(({settings}) => settings);
    const [imagePreviewUrl, setProfileImgUrl] = useState('https://via.placeholder.com/300x300');

    useEffect(() => {
        $http.get(base_url + "user/userInfo")
        .then((response) => {
            setFirstName(response.data.user.first_name);
            setProfileImgUrl(response.data.user.profile_pic ? 
                (public_url + 'upload/img/' + response.data.user.profile_pic) :
                'https://via.placeholder.com/300x300');
            setLastName(response.data.user.last_name);
            setJobTitle(response.data.user.job_title);
            setUserRole(response.data.user.user_type);
            setContactNumber(response.data.user.mobile);
            setStatus(response.data.user.status);
            setEmailAddress(response.data.user.email);
            setLoader(true);
        });
    }, []);

    const SaveProfile = () => {
        const frmData = new FormData();
        frmData.append('first_name', first_name);
        frmData.append('last_name', last_name);
        frmData.append('job_title', job_title);
        frmData.append('user_type', user_role);
        frmData.append('mobile', contact_number);
        frmData.append('status', status);
        frmData.append('email', email_address);
        frmData.append('profile_pic', profile_img);
            
        $http.post(base_url + "user/saveUser", frmData)
        .then((response) => {
            if (response.data.status==="success") {
                NotificationManager.success(response.data.msg, "Success!", 5000, () => {
                });
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 5000, () => {
                });
            }
        });
    }
    const _handleImageChange = (e) =>{
        e.preventDefault();
  
        let reader = new FileReader();
        let file = e.target.files[0];
        reader.onloadend = () => {
            setProfileImg(file);
            setProfileImgUrl(reader.result);
        }
        reader.readAsDataURL(file)
    }

    return (
        <div className="app-wrapper">
            <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.settings.profile"/>}/>
            {loader ? (

                <div className="row">
                    <div className="col-xl-3 col-lg-4 col-md-5 col-12">
                        <div className="jr-card pb-2">
                            <div className="jr-card-thumb">
                                <img className="card-img-top img-fluid" alt="products" src={imagePreviewUrl}/>
                            </div>
                            <input type="file" id="profile_img" accept="image/*" 
                                    name="profile_img" style={{display: "none"}}
                                    onChange={(event) => {
                                        _handleImageChange(event)
                                    }}/>
                            <div className="jr-card-social text-center">
                                <Fab className="jr-fab-btn bg-light-blue accent-2 text-white" onClick={(e)=>{document.getElementById('profile_img').click()}}>
                                    <i className="zmdi zmdi-cloud-upload zmdi-hc-2x"/>
                                </Fab>
                            </div>
                        </div>
                    </div>
                    <CardBox styleName="col-xl-9 col-lg-8 col-md-7 col-12">
                        <form className="row" autoComplete="off">
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="first_name"
                                    label="First Name"
                                    value={first_name}
                                    onChange={(event) => setFirstName(event.target.value)}
                                    margin="normal"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="last_name"
                                    label="Last Name"
                                    value={last_name}
                                    onChange={(event) => setLastName(event.target.value)}
                                    margin="normal"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="job_title"
                                    label="Job Title"
                                    value={job_title}
                                    onChange={(event) => setJobTitle(event.target.value)}
                                    margin="normal"
                                    fullWidth
                                    />
                            </div>
                            
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="user_role"
                                    label="User Role"
                                    value={user_role}
                                    onChange={(event) => setUserRole(event.target.value)}
                                    margin="normal"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="contact_number"
                                    label="Contact Number"
                                    value={contact_number}
                                    onChange={(event) => setContactNumber(event.target.value)}
                                    margin="normal"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="status"
                                    label="Status"
                                    value={status}
                                    onChange={(event) => setStatus(event.target.value)}
                                    margin="normal"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="password"
                                    label="Password"
                                    value={password}
                                    onChange={(event) => setPassword(event.target.value)}
                                    margin="normal"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="email_address"
                                    label="Email Address"
                                    value={email_address}
                                    onChange={(event) => setEmailAddress(event.target.value)}
                                    margin="normal"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-12 col-12 text-right">
                                <Button variant="contained" color="primary" className="jr-btn" onClick={() => SaveProfile()}>
                                    Save
                                </Button>
                            </div>
                        </form>
                    </CardBox>
                </div>
            ):(
                <div className="loader-view"
                        style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                    <CircularProgress/>
                </div>        

            )}
            <NotificationContainer/>
        </div>
    );
}

export default Profile;